.homepage {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  min-height: 95vh;
  width: 100%;
}
.info {
  margin-top: 5rem;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.name {
  /* color: var(--main); */
  font-weight: 700;
  font-size: 4rem;
  margin-top: 0.5rem;
}
.title {
  color: var(--main);
  font-weight: 500;
  font-size: 1.35rem;
}
.display-pic {
  background-color: var(--main);
  border-radius: 50%;
  width: 10rem;
  height: 10rem;
}
.links {
  margin: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.links li {
  color: white;
  background-color: var(--main);
  border-radius: 50%;
  font-size: 20px;
  margin: 0.5rem;

  display: flex;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
}
.btn {
  background-color: var(--main);
  color: white;
  border: 0;
  border-radius: 15px;
  padding: 16px;
  width: 90%;
  outline: 0;
}
.btn:hover {
  background-color: var(--main-light);
  cursor: pointer;
}
@media only screen and (min-width: 768px) {
  .btn {
    width: 25%;
  }
}
